<template>
  <div class="levertransactions">
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <div class="box">
      <div class="table">
        <div class="th">
          <td class="td">时间</td>
          <td class="td">单价</td>
          <td class="td">数量</td>
          <td class="td">操作</td>
        </div>
        <div class="tr" v-for="(item, index) in list" :key="index">
          <td class="td">{{ item.open_time }}</td>
          <td class="td">{{ item.price }}</td>
          <td class="td">{{ item.margin }}</td>
          <td class="td td-2">
            <span class="btn" @click="calcel(index)">撤单</span>
            <span class="btn" @click="position(index)">平仓</span>
          </td>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import { getPositionList, cancalPosition, closePosition } from "@/api/asset";

export default {
  components: {
    AoNav
  },
  data() {
    return {
      list: [],
      page: 1
    };
  },
  mounted() {
    var data = {
      page: this.page,
      currency: "ETC"
    };
    getPositionList(data).then(res => {
      this.list = this.list.concat(res);
    });
  },
  methods: {
    calcel(index) {
      cancalPosition({ id: this.list[index].deal_id }).then(() => {
        this.list.splice(index, 1);
      });
    },
    position(index) {
      var { currency, multiple, type, deal_id } = this.list[index];
      closePosition({ currency, multiple, type, deal_id }).then(res => {
        this.list.splice(index, 1);
      });
    },
    pagechange(page) {
      this.page = page;
    }
  }
};
</script>

<style  lang="scss">
.levertransactions {
  div {
    box-sizing: border-box;
  }

  .box {
    min-height: 500px;
    background: #1a243b;
    width: 97%;
    margin: 30px auto;
    padding: 30px;
  }
  .th,
  .tr {
    display: flex;
    padding: 8px 0;
    color: #637085;
  }
  .th {
    border-bottom: 1px solid #2e333c;
  }
  .td {
    flex: 1;
  }
  .td-2 {
    flex: 2;
  }
  .th .td,
  .tr .td {
    color: #637085;
    font-size: 14px;
    text-align: center;
  }
  .btn {
    border-radius: 3px;
    color: #fff;
    background-color: #638bd4;
    cursor: pointer;
    font-size: 14px;
    border: none;
    padding: 0 5px;
    line-height: 33px;
    text-align: center;
  }
}
</style>



